import { CanvasController } from '../utils'
const state = {
  /************************
   * canvasData:当前正在编辑的canvasController实例
   * oldCanvasData:保存过的canvasController实例,数据格式[canvasController1,canvasController2,....]
   * imgUrlList:素材区的图片url
   ************************/
  canvasData: new CanvasController(),
  imgUrlList: [],
  oldData: localStorage.getItem('canvasData') ? JSON.parse(localStorage.getItem('canvasData')) : [],
  collect: ['/static/img/theme-demo3.0455a2f.png']
}
export default state



