import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

export default new VueRouter({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: () => import('@/components/home/home.vue')
    },
    {
      path: '/design',
      name: 'Design',
      component: () => import('@/components/design.vue')
    },
    {
      path: '/theme',
      name: 'theme',
      component: () => import('@/components/theme/theme.vue')
    },
    {
      path: '/mine',
      name: ' mine',
      component: () => import('@/components/mine/mine.vue')
    },
    {
      path: '/star',
      name: 'star',
      component: () => import('@/components/collection/collection')
    }
  ]
})



