import { mapMutations, mapGetters, mapActions } from 'vuex'

export const page = {
  computed: {
    ...mapGetters([
      'testData',
      'canvasData'
    ])
  },
  methods: {
    ...mapMutations({
      initCanvas: 'INIT_CANVAS',
      handleLayerChange: 'LAYER_ON_CHANGE'
    }),
    ...mapActions([
      'initCanvas',
      'handleLayerChange'
    ])
  }
}



